<template>
  <div id="process-item-list">
    <slot v-if="dummydata"></slot>
    <table v-if="items.length">
      <thead>
        <tr>
          <!-- avatar -->

          <th class="sticky icon" style="left: 0"></th>

          <!-- ... -->

          <!-- title -->

          <th v-if="title" class="sticky" style="left: 36px">
            <slot name="titleHead" :title="title.label">
              {{ title.label }}
            </slot>
          </th>

          <!-- ... -->

          <!-- mlcondition -->

          <th v-if="mlCondition">
            <slot name="titleHead" :title="mlCondition.label">
              {{ mlCondition.label }}
            </slot>
          </th>

          <!-- ... -->

          <!-- columns -->

          <th v-for="column in _columns" :key="column.id">
            {{ column.label }}
          </th>

          <!-- ... -->

          <th class="" style="right: 0"></th>

          <!-- ... -->
        </tr>
      </thead>
      <tbody>
        <template v-for="group in groupKey">
          <tr
            v-if="group.name && groupRequestListName(group.name)"
            :key="group.name"
            class="background"
          >
            <td colspan="4" class="sticky" style="left: 0">
              <div v-if="group.name" :key="group.name" class="row">
                <div class="group-by col-auto">
                  <CheckboxField
                    v-if="group.checkboxRequired"
                    :value="group.checked"
                    class="col-auto check-group-by"
                    @input="updateSelectionGroup(group)"
                  />
                  <div class="col-auto">
                    <div class="groupLabel">
                      {{ group.name + " (" + group.totalCount + ")" }}
                    </div>
                  </div>
                  <BaseIcon
                    :name="group.expand ? 'mdi-chevron-down' : 'mdi-chevron-up'"
                    class="cursor-pointer q-ml-sm"
                    @click="group.expand = !group.expand"
                  />
                </div>
                <div class="q-ml-sm">
                  <BaseActionButton
                    v-if="groupSelected(group.name) && group.actions.length"
                    v-tooltip.top="'Actions'"
                    icon="eva-more-horizontal-outline"
                    size="16px"
                    color="primary-5"
                  >
                    <q-menu
                      transition-show="scale"
                      transition-hide="scale"
                      anchor="bottom end"
                      self="top right"
                    >
                      <ListItem
                        v-for="action in group.actions"
                        :key="action.id"
                        :label="action.name"
                        :icon="action.icon"
                        :color="action.color"
                        @click="$emit('action', action, group)"
                      />
                    </q-menu>
                  </BaseActionButton>
                </div>
              </div>
            </td>
          </tr>

          <template v-for="item in groupRequestList(group.name)">
            <tr
              v-if="item.visible && group.expand"
              :key="group.name + '-' + item.id"
              class="background"
              @click="expandRow(item)"
            >
              <!-- avatar -->

              <td
                class="sticky"
                style="left: 0; width: 36px"
                :style="{ background: actionColor(item.lastAction) }"
              >
                <div class="items-center justify-center" style="display: flex">
                  <slot name="avatar" :item="item">
                    <BaseIcon :name="item.icon" color="secondary" />
                  </slot>
                  <template v-if="item.subWorkflow">
                    <BaseIcon
                      v-if="item.subWorkflow.length"
                      :name="
                        item.expandSubworkflow
                          ? 'mdi-chevron-down'
                          : 'mdi-chevron-up'
                      "
                      class="cursor-pointer q-ml-sm"
                    />
                  </template>
                </div>
              </td>

              <!-- ... -->

              <!-- title -->

              <td
                v-if="title"
                class="sticky"
                style="
                  left: 36px;
                  left: 0;
                  width: 20%;
                  color: #007bff;
                  cursor: pointer;
                "
              >
                <div>
                  <slot name="title" :item="item" :title="item[title.name]">
                    <div
                      class="font-medium ellipsis"
                      :class="item.link ? 'text-underline' : ''"
                      style="
                        overflow: hidden !important;
                        white-space: unset !important;
                        max-width: 250px;
                      "
                    >
                      {{ item[title.name] }}
                    </div>
                  </slot>
                </div>
              </td>

              <!-- ... -->

              <!-- mlcondition -->

              <td v-if="mlCondition">
                <slot
                  name="mlcondition"
                  :item="item"
                  :mlcondition="item[mlCondition.name]"
                >
                  <div class="font-medium q-mr-xl">
                    {{ item[mlCondition.name] }}
                  </div>
                </slot>
              </td>

              <!-- ... -->

              <!-- columns -->

              <td v-for="column in _columns" :key="column.id">
                <slot
                  :name="column.name"
                  :value="item[column.name]"
                  :item="item"
                >
                  {{ item[column.name] || "-" }}
                </slot>
              </td>

              <!-- ... -->

              <!-- actions -->

              <td class="sticky" style="right: 0">
                <slot name="actions" :item="item"> </slot>
              </td>

              <!-- ... -->
            </tr>
            <template v-if="item.subWorkflow">
              <template v-if="item.subWorkflow.length">
                <tr
                  v-if="item.expandSubworkflow"
                  :key="group.name + '+' + item.id"
                >
                  <td :colspan="_columns.length + 3">
                    <table
                      :key="`subtable${group.id}`"
                      style="
                        margin-left: 20px;
                        border: 1px solid var(--divider-color);
                        table-layout: fixed;
                        max-width: 63%;
                        min-width: calc(59% - 1px);
                        border-collapse: collapse;
                        white-space: nowrap;
                      "
                    >
                      <thead>
                        <tr>
                          <th
                            class="sticky icon"
                            name="icon"
                            style="
                              left: 0;
                              background-color: rgb(222 217 247);
                              width: 2%;
                            "
                          ></th>

                          <th
                            v-if="title"
                            class="sticky"
                            name="title"
                            style="
                              left: 36px;
                              background-color: rgb(222 217 247);
                              width: 2%;
                            "
                          >
                            <div
                              class="font-medium ellipsis"
                              style="max-width: 250px"
                            >
                              {{ title.label }}
                            </div>
                          </th>

                          <th
                            v-for="column in _subWorkflowColumns"
                            :key="column.id"
                            style="
                              background-color: rgb(222 217 247);
                              width: 2%;
                            "
                          >
                            {{ column.label }}
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        <template v-for="subItem in item.subWorkflow">
                          <tr :key="subItem.requestNo">
                            <td
                              class="sticky icon"
                              name="avatar"
                              style="left: 0"
                            >
                              <div
                                class="row items-center sticky justify-center"
                              >
                                <BaseIcon
                                  v-tooltip.top="subItem.iconTooltip"
                                  :name="subItem.icon"
                                  :color="subItem.color"
                                />
                              </div>
                            </td>

                            <td
                              class="sticky"
                              name="title"
                              style="
                                left: 0;
                                width: 20%;
                                color: #007bff;
                                cursor: pointer;
                              "
                            >
                              <div
                                style="
                                  overflow: hidden !important;
                                  white-space: unset !important;
                                  width: 220px;
                                "
                                class="row items-center text-underline"
                                @click="openSubProcess(subItem)"
                              >
                                {{ subItem.requestNo || "-" }}
                              </div>
                            </td>
                            <td class="sub-background">
                              {{ subItem.stage }}
                            </td>
                            <td class="sub-background">
                              {{ subItem.raisedBy }}
                            </td>
                            <td class="sub-background">
                              {{ subItem.raisedAt }}
                            </td>
                            <td>
                              {{ subItem.duration }}
                            </td>
                            <td class="sticky" style="right: 0">
                              <slot name="subactions" :item="subItem"> </slot>
                            </td>
                          </tr>
                        </template>
                      </tbody>
                    </table>
                  </td>
                </tr>
              </template>
            </template>
          </template>
        </template>
      </tbody>
    </table>
  </div>
</template>

<script>
import CheckboxField from "@/components/common/form/checkbox-field/CheckboxField.vue";
import ListItem from "@/components/common/ListItem.vue";
import { mapState } from "vuex";

export default {
  name: "ItemList",

  components: {
    CheckboxField,
    ListItem,
  },

  props: {
    columns: {
      type: Array,
      required: true,
    },

    items: {
      type: Array,
      required: true,
    },

    groupKey: {
      type: Array,
      default: () => [],
    },

    groupItemSelected: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      dummydata: false,
    };
  },

  computed: {
    ...mapState(["darkTheme"]),

    _columns() {
      return this.columns.filter(
        (column) =>
          column.type !== "TITLE" &&
          column.type !== "MLCONDITION" &&
          column.isVisible
      );
    },

    title() {
      return this.columns.find((column) => column.type === "TITLE");
    },

    mlCondition() {
      return this.columns.find((column) => column.type === "MLCONDITION");
    },

    _subWorkflowColumns() {
      let tableColumns = [
        {
          id: this.$nano.id(),
          name: "stage",
          label: "Stage",
          type: "SINGLE_SELECT",
          isVisible: true,
          isSortable: true,
          isGroupable: false,
        },
        {
          id: this.$nano.id(),
          name: "raisedBy",
          label: "Raised By",
          type: "SINGLE_SELECT",
          isVisible: true,
          isSortable: true,
          isGroupable: false,
        },

        {
          id: this.$nano.id(),
          name: "raisedAt",
          label: "Raised On",
          type: "DATE",
          isVisible: true,
          isSortable: true,
          isGroupable: false,
        },
        {
          id: this.$nano.id(),
          name: "duration",
          label: "Duration",
          type: "SINGLE_SELECT",
          isVisible: true,
          isSortable: true,
          isGroupable: false,
        },
      ];
      return tableColumns;
    },
  },

  methods: {
    groupRequestList(group) {
      return this.items.filter((row) => row.key === group);
    },

    groupRequestListName(group) {
      const matchingRows = this.items.filter((row) => row.key === group);
      if (
        matchingRows.length > 0 &&
        matchingRows.some((row) => row.visible === true)
      ) {
        return true;
      }
      return false;
    },

    groupSelected(group) {
      let groupList = this.items.filter((row) => row.key === group);
      let count = 0;
      groupList.forEach((process) => {
        if (process.checked) {
          count++;
        }
      });
      //this.$emit("groupItemSelected", count ? true : false);
      return count > 1 ? true : false;
    },

    updateSelectionGroup(group) {
      this.$emit("group", group);
    },

    actionColor(action) {
      if (this.darkTheme) {
        if (!action) {
          return "#7f7f7f80";
        }
        if (action.toLowerCase().indexOf("reply") !== -1) {
          return "#ca811680";
        } else if (action.toLowerCase().indexOf("forward") !== -1) {
          return "#fcf5bc80";
        } else if (action.toLowerCase().indexOf("approve") !== -1) {
          return "#d2bc0980";
        } else if (action.toLowerCase().indexOf("reject") !== -1) {
          return "#c9141480";
        } else {
          return "#7f7f7f80";
        }
      } else {
        if (!action) {
          return "#ffffff";
        }
        if (action.toLowerCase().indexOf("reply") !== -1) {
          return "#f9e6ca";
        } else if (action.toLowerCase().indexOf("forward") !== -1) {
          return "#fcf5bc";
        } else if (action.toLowerCase().indexOf("approve") !== -1) {
          return "#bfe2c0";
        } else if (action.toLowerCase().indexOf("reject") !== -1) {
          return "#f9c3c3";
        } else {
          return "#ffffff";
        }
      }
    },

    expandRow(item) {
      item.expandSubworkflow = !item.expandSubworkflow;
      this.dummydata = !this.dummydata;
    },

    openSubProcess(item) {
      this.$emit("openSubWorkflow", item);
    },
  },
};
</script>

