                      <div class="col-auto">
                        <BaseIcon
                          name="mdi-file-link-outline"
                          color="red"
                          class="q-mr-sm"
                        />
                      </div>
                      <div class="col-auto text-underline">123456987.msg</div>
                    </div> -->
                  </div>
                  <div class="col-auto q-mr-sm">
