            id: item.id,
            name: item.name,
            description: item.description,
            filterJson: item.filterJson,
            filterFrom: item.filterFrom,
            createdAt: item.createdAt,
            modifiedAt: item.modifiedAt,
            createdBy: item.createdBy,
            modifiedBy: item.modifiedBy,
            isDeleted: true,
          };
        }
      });
    },

    async save() {
      this.success = false;
      let filter = JSON.stringify(this.filterBy);
      let filtering = [
        {
          uId: this.selectedWorkflow.id,
          refreshCount: this.refreshCount,
          node: this.nodeType,
          filters: filter,
        },
      ];
      let filterss = JSON.stringify(filtering);
      let userId = [this.$store.state.session.id];
      let now = new Date();
      let timestamp = now.toISOString();
      let name = "";
      let description = "";
      if (this.nodeType === "inbox") {
        name = "My Inbox" + "  " + this.selectedNode + "  " + timestamp;
        description = "My Inbox";
      } else {
        name =
          this.selectedWorkflow.name +
          "  " +
          this.selectedNode +
          "  " +
          timestamp;
        description = this.selectedWorkflow.description;
      }

      let input = {
        name: name,
        description: description,
        filterJson: filterss,
        filterFrom: "workflowInbox",
        pinDashboard: true,
        assignTo: JSON.stringify(userId),
      };
      const { error } = await dashboard.saveDashboard(input);
      if (error) {
        this.$alert.warning("The Name Already Registered ");
        return;
      }
      //this.success = true;
      this.dashboard = true;
      this.getFilterList();
      this.$alert.success("Pinned SuccessFully");
    },
  },
};
</script>

