<template>
  <Confirm
    :value="value"
    icon="mdi-cellphone-link"
    icon-color="secondary"
    @input="handleInput"
  >
    <template #title>Upload files from another device</template>

    <template #default>
      <div class="q-py-md row qr-contnt">
        <img :src="qrImg" />

        <div class="row qr-info">
          <BaseIcon
            v-tooltip.top="tooltip"
            inherit-color
            name="eva-alert-circle-outline"
            class="info-icon"
          />
          <p>Keep the form open while uploading files.</p>
        </div>
      </div>
    </template>
  </Confirm>
</template>

<script>
import Confirm from "@/components/common/popup/Confirm.vue";
import { repository } from "@/api/factory.js";
export default {
  name: "GenerateQr",

  components: { Confirm },

  props: {
    value: {
      type: Boolean,
      required: true,
    },
    isReadonly: {
      type: Boolean,
      default: false,
    },
    repositoryId: {
      type: Number,
    },
    fieldId: {
      type: String,
    },
  },
  data() {
    return {
      qrImg: "",
    };
  },
  watch: {
    value: {
      deep: true,
      immediate: true,
      handler() {
        if (this.value) {
          this.getQr();
        }
      },
    },
  },

  methods: {
    handleInput(value) {
      this.$emit("input", value);
      console.log(this.$store.state.session, "this.$store.state.session");
    },
    async getQr() {
      let orginurl =
        location.origin == "http://localhost:8080"
          ? "http://52.172.32.88/app"
          : location.origin;
      let input = {
        url: orginurl + "/mobileUpload",
        jsonData: JSON.stringify([
          { repositoryId: this.repositoryId, fieldId: this.fieldId },
        ]),
        status: 0,
      };
      console.log(input, "input");
      const { error, payload } = await repository.getQrcode(input);
      console.log(error, payload, "error, payload");
      console.log(JSON.parse(payload.data), "url");
      const { id, output } = JSON.parse(payload.data);
      this.qrImg = "data:image/png;base64," + output;
      console.log(this.qrImg, "this.qrImg", id);
      this.$emit("checkUploadId", id);
    },
  },
};
</script>

<style lang="scss" scoped>
.qr-contnt {
  justify-content: center;
}
#confirm .footer {
  display: none !important;
}
.qr-info {
  width: 100%;
  color: red;
}
.qr-info p {
  margin: 0px;
}
.info-icon {
  margin-right: 3px;
}
</style>
